import { TitleCasePipe } from '@angular/common';
import { Component, OnInit } from '@angular/core';
import { FormArray, FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { CommonService } from 'src/app/core/services/common.service';
import { PromotionService } from 'src/app/core/services/promotion.service';
import { ToastNotificationService } from 'src/app/core/services/toast-notification.service';

@Component({
  selector: 'app-counter-disc-modal',
  templateUrl: './counter-disc-modal.component.html',
  styleUrls: ['./counter-disc-modal.component.scss']
})
export class CounterDiscModalComponent implements OnInit {
  
  public currentUser = JSON.parse(localStorage.getItem("currentUser"))
  public promotionForm: any;
  public submitted: boolean = false;
  public today: Date;
  types: string[] = ['DISCOUNT', 'PRODUCT', 'FLAT'];  // ['DISCOUNT', 'PRODUCT', 'FLAT', 'COUPONS'];
  productsIdList: any;
  selectedProductIds: string[] = [];
  saveOn: boolean = true;

  constructor(private formBuilder: FormBuilder,
    public router: Router, private commonService: CommonService,
    private toastService: ToastNotificationService, public dialog: MatDialog,
    private titleCasePipe: TitleCasePipe,
    // @Inject(MAT_DIALOG_DATA) public data: any,
    public dialogRef: MatDialogRef<CounterDiscModalComponent>,
    private promotionService: PromotionService
  ) {
    this.today = new Date();
  }

  ngOnInit(): void {
    try {
      this.promotionForm = this.formBuilder.group({
          counterDetails: new FormArray([]),
        },
      );
      this.getCounterDiscountAmt()
    } catch {

    }
  }

  get f() {
    return this.promotionForm.controls;
  }

  get counterDiscountControls() {
    return this.promotionForm.get('counterDetails') as FormArray;
  }
  
  getCounterDiscountAmt(){
    this.promotionService.getCounterDiscountForm({}, this.currentUser.id).then(res => {
      if(res.success) {
        if(res.data.length > 0) {
          this.saveOn = false
        } else {
          this.saveOn = true
        }
        let counterArr = res.data;
        if (counterArr.length) {
          for (let i = 0; i < counterArr.length; i++) {
            this.counterDiscountControls.push(
              new FormGroup({
                minimum: new FormControl(counterArr[i].minimum),
                maximum: new FormControl(counterArr[i].maximum),
                percent: new FormControl(counterArr[i].percent),
                id: new FormControl(counterArr[i].id),
              })
            )
          }
        }
      }
    })
  }

  cancel() {
    this.dialogRef.close();
  }

  createCounterDetails(): FormGroup {
    return new FormGroup({
      minimum: new FormControl("", [Validators.required, this.minimumValidator(0)]),
      maximum: new FormControl(""),
      percent: new FormControl("",),
    });
  }
  
  addCounterDetails(): void {
    const data = this.promotionForm.get('counterDetails') as FormArray;
    data.push(this.createCounterDetails());
  }
  
  deleteCounterDetails(index: number): void {
    const data = this.promotionForm.get('counterDetails') as FormArray;
    data.removeAt(index)
  }

  onSave() {
    this.submitted = true;
    console.log(this.promotionForm)
    if (this.promotionForm.invalid) {
      this.toastService.toastMsg({
        title: "Error",
        content: "Fill All Required Fields.",
      });
      return false;
    } else {
      const arr = this.promotionForm.value.counterDetails
      arr.forEach(item => item.userId = this.currentUser.id)
      this.promotionService.counterDiscountForm({data : arr }).then( res => {
        if(res.success) {
          this.dialogRef.close();
          this.commonService.notifyDataAdded()
          this.toastService.toastMsg({
            title: "Success",
            content: "Discount Addedd Successfully!!!",
          });
        }
      })
    }
  }

  updateCounterDiscount(){
    this.submitted = true;
    console.log(this.promotionForm)
    if (this.promotionForm.invalid) {
      this.toastService.toastMsg({
        title: "Error",
        content: "Fill All Required Fields.",
      });
      return false;
    } else {
      const arr = this.promotionForm.value.counterDetails
      arr.forEach(item => item.userId = this.currentUser.id)
      this.promotionService.updateCounterDiscount({data : arr }, this.currentUser.id).then( res => {
        if(res.success) {
          this.dialogRef.close();
          this.commonService.notifyDataAdded()
          this.toastService.toastMsg({
            title: "Success",
            content: "Promotion Addedd Successfully!!!",
          });
        }
      })
    }
  }

  setMinLength(index: number): number {
    const controls = this.counterDiscountControls.controls;
  
    // Check if there is a previous row
    if (index > 0) {
      const prevMaximum = controls[index - 1].get('maximum').value;
      return prevMaximum;
    } else {
      // If it's the first row, return an initial value (e.g., 0)
      return 0;
    }
  }

  minimumValidator(index: number) {
    return (control) => {
      const controls = this.counterDiscountControls.controls;
      const prevMaximum = index > 0 ? controls[index - 1].get('maximum').value : -Infinity;
      const currentMinimum = control.value;
      if (currentMinimum <= prevMaximum) {
        return { minimumError: true };
      } else {
        return null;
      }
    };
  }
  
}
