import { TitleCasePipe } from '@angular/common';
import { Component, Inject, OnInit } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialog, MatDialogRef } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { CommonService } from 'src/app/core/services/common.service';
import { ProductService } from 'src/app/core/services/product.service';
import { PromotionService } from 'src/app/core/services/promotion.service';
import { ToastNotificationService } from 'src/app/core/services/toast-notification.service';

@Component({
  selector: 'app-assign-product-to-promotion',
  templateUrl: './assign-product-to-promotion.component.html',
  styleUrls: ['./assign-product-to-promotion.component.scss']
})
export class AssignProductToPromotionComponent implements OnInit {

  public currentUser = JSON.parse(localStorage.getItem("currentUser"))
  formBuilder: any;
  promotionForm: any;
  promotionId: any;
  productsIdList: any;
  selectedProductIds: string[] = [];

  constructor(public router: Router, private commonService: CommonService,
    private toastService: ToastNotificationService, public dialog: MatDialog,
    private titleCasePipe: TitleCasePipe,
    @Inject(MAT_DIALOG_DATA) public data: any,
    public dialogRef: MatDialogRef<AssignProductToPromotionComponent>,
    private promotionService: PromotionService,
    private productService: ProductService,) { 
      this.promotionId = this.data.promotionId;
    }

  ngOnInit(): void {
    this.getAllProductsAvailableForPromotion()
  }

  getAllProductsAvailableForPromotion() {
    this.promotionService.getAllProductsAvailableForPromotion({
      data: {}, 
      userId: this.currentUser.id, 
      page: 1, 
      size: 1000, 
      productType: 'GOODS'
  }).then((res) => {
      if(res.success) {
        this.productsIdList = res.data.rows;
      }
    },(err) => {
        if (err.error.status === 404) {
            this.toastService.toastMsg({
              title: 'Error',
              content: 'Data Not Found!!!'
            })
        } else {
          this.toastService.toastMsg({
            title: 'Error',
            content: this.titleCasePipe.transform(err.error.error_message)
          })
        }
      });
  }

  onSave() {
    const payload = {
      promotionId: this.promotionId,
      productArray: this.selectedProductIds.map(productId => ({ productId }))
    };
    if(this.selectedProductIds.length > 0) {
      this.promotionService.assignProductsToPromotions(payload, this.currentUser.id)
      .then((res) => {
        if(res.success) {
          this.toastService.toastMsg({
            title: 'Success',
            content: "Selected products assigned Successfully!!!"
          })
          this.dialogRef.close();
        }
      })
      .catch((error) => {
        if(error.status == 409) {
          this.toastService.toastMsg({
            title: 'Error',
            content: "The product shall be already assigned to another promotion"
          })
        } else {
          this.toastService.toastMsg({
            title: 'Error',
            content: this.titleCasePipe.transform(error.error.error_message)
          })
        }
      });
    } else {
      this.toastService.toastMsg({
        title: 'Error',
        content: 'please select atleast one product'
      })
    }
  }

  cancel() {
    this.dialogRef.close();
  }

}
